import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { FormsModule } from '@angular/forms';

import { UserService } from 'src/app/api/controllers/User';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { SharedToasterService } from '../../../../../shared/services/shared-toaster.service';
import { SharedService } from '../../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as moment from 'moment';

@Component({
  selector: 'app-add-visitor',
  templateUrl: './add-visitor.component.html',
  styleUrls: ['./add-visitor.component.scss']
})
export class AddVisitorComponent implements OnInit {

  isEditVisitor:boolean = false;

  isVisitorSubmitted:boolean = false;

  isError:boolean = false;
  errorMessage:string = "";

  visitor:any = {expectedVisitorInTime:''};

  visitByData:any;
  visitTypeId:string = "";

  apartmentUnitId:number;
  minDate;
  visitCategoryData;

  @Input ('index') index;
  @Input ('array') visitorArray;

  @Output() public outputParams = new EventEmitter();

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private apartmentService:ApartmentService,
    private visitorService:VisitorService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private SharedToaster:SharedToasterService,

  ) {
    this.minDate = moment(new Date()).format('YYYY-MM-DD');
    this.visitor.expectedVisitorInTime =  moment(new Date());
   }


  isArrayOne(){
  	return this.visitorArray.length > 1 ? true : false;
  }

  deleteVisitor(){
  	this.visitorArray.splice(this.index, 1);
    this.outputParams.emit(this.visitorArray);
  }

  submitAddVisitorForm(form) {

  	this.isVisitorSubmitted = true;
    this.isError = false;

    if(!this.isEditVisitor) {

      let visitorDetails = {
    		"apartmentUnitId": this.apartmentUnitId,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "expectedVisitorName": this.visitor.expectedVisitorName,
        "expectedVisitorCount": this.visitor.expectedVisitorCount,
        "expectedVisitorPhone": this.visitor.expectedVisitorPhone.toString(),
        "expectedVisitorInTime": this.visitor.expectedVisitorInTime,
        "expectedVisitorOutTime": this.visitor.expectedVisitorOutTime,
        "expectedDuration": this.visitor.expectedDurationInHours,
        "meetingPersonId": 1,
        "visitTypeId": parseInt(this.visitTypeId),
        "expectedVisitorsOrg": "string",
        "entryGateId": null,
        "exitGateId": null,
        "purpose": this.visitor.purpose,
        "enteredBy": parseInt(this.cookieService.get('userId')),
        "isActive": true,
        "insertedBy":parseInt(this.cookieService.get('userId')),
        "insertedOn": "2019-11-18T15:27:41.030Z",
        "updatedBy": 0,
        "updatedOn": "2019-11-18T15:27:41.030Z"
      }

    let params = {
      expectedVisitor: visitorDetails
    }

    this.visitorService.addExpectedVisitor(params).subscribe((res:any) => {
          
        if(res.message) {
          this.isVisitorSubmitted = true;
          this.sharedService.setAlertMessage("Visitor added successfully");
          this.router.navigate(['user/visitor/my-visitor']);
        }
        else {

          this.isVisitorSubmitted = false;
          this.isError = true;
          this.errorMessage = res.errorMessage;
        }

      },
      error => {
        
    		this.isVisitorSubmitted = false;
    		this.isError = true;
    		this.errorMessage = error;

      });

    }	

    else {

      let visitorDetails = {
        
        "expectedVisitorId": this.route.params['value'].id,
        "apartmentUnitId": this.apartmentUnitId,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "expectedVisitorName": this.visitor.expectedVisitorName,
        "expectedVisitorCount": this.visitor.expectedVisitorCount,
        "expectedVisitorPhone": this.visitor.expectedVisitorPhone.toString(),
        "expectedVisitorInTime": this.visitor.expectedVisitorInTime,
        "expectedVisitorOutTime": this.visitor.expectedVisitorOutTime,
        "expectedDuration": this.visitor.expectedDurationInHours,
        "meetingPersonId": 1,
        "visitTypeId": parseInt(this.visitTypeId),
        "expectedVisitorsOrg": "string",
        "entryGateId": null,
        "exitGateId": null,
        "purpose": this.visitor.purpose,
        "enteredBy": parseInt(this.cookieService.get('userId')),
        "isActive": true,
        "insertedBy":0,
        "insertedOn": "2019-11-18T15:27:41.030Z",
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": "2019-11-18T15:27:41.030Z"
      }

      let params = {
        expectedVisitor: visitorDetails
      }


      this.visitorService.updateExpectedVisitor(params).subscribe((res:any) => {
          
        if(res.message) {
          this.isVisitorSubmitted = true;
          this.sharedService.setAlertMessage("Visitor updated successfully");
          this.router.navigate(['user/visitor/my-visitor']);
        }
        else {

          this.isVisitorSubmitted = false;
          this.isError = true;
          this.errorMessage = res.errorMessage;
        }

      },
      error => {
        
        this.isVisitorSubmitted = false;
        this.isError = true;
        this.errorMessage = error;

      });



    }

  }

  ngOnInit() {

  	this.visitor = {};

    if(this.route.params['value'].id != undefined){
      this.isEditVisitor = true;
    }

    if(this.route.params['value'].id != undefined) {

      this.isEditVisitor = true;
      
      this.visitorService.getExpectedVisitorById(this.route.params['value'].id).subscribe((res:any) => {
          
        this.visitor = res[0];
        this.visitor.purpose = '';
        this.visitTypeId = this.visitor.visitTypeId;

      },
      error => {
        
      }
      );

    }

    let visitParams = {
      LookupTypeId: 15
    }

    //visit type
    this.lookupService.getLookupValueByLookupTypeId(visitParams).subscribe((res:any) => {
          
      this.visitByData = res;

    },
    error => {
      
    }
    );

    let params = {
      LookupTypeId: 9
    }

    //visit category
    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {
          
      console.log(res);

    },
    error => {
      
    }
    );

    let visitCategoryParams = {
      LookupTypeId: 100
    }

    this.lookupService.getLookupValueByLookupTypeId(visitCategoryParams).subscribe((res:any) => {
          
      this.visitCategoryData = res;

    },
    error => {
      
    }
    );

    let apartmentUnitParams = {
      userId: parseInt(this.cookieService.get('userId'))
    }

	this.apartmentService.getApartmentBlockUnitByUserId(apartmentUnitParams).subscribe((res:any) => {

		this.apartmentUnitId = res[0].apartmentBlockUnitId;

	},
	error => {

	}
  );
  this.visitor.expectedVisitorInTime = moment(new Date());
  }

  expectedDurationChange(){
    if(this.minDate > moment(this.visitor.expectedVisitorInTime).format('YYYY-MM-DD')){
      console.log("min date");
      this.visitor.expectedVisitorInTime = undefined;
      this.SharedToaster.openSnackBar("Please Select valid Date",'');
      return;
    }
    if(this.minDate > moment(this.visitor.expectedVisitorOutTime).format('YYYY-MM-DD')){
      console.log(this.visitor.expectedVisitorOutTime);
      this.visitor.expectedVisitorOutTime = undefined;
      console.log(this.visitor.expectedVisitorOutTime);
      this.SharedToaster.openSnackBar("Please Select valid Date",'');
      return;
    }
    let inDate = moment(this.visitor.expectedVisitorInTime);
    let outDate = moment(this.visitor.expectedVisitorOutTime);
    var diffDuration = moment.duration(outDate.diff(inDate)); 
    this.visitor.expectedDuration = diffDuration.days()+" days "+ this.isSingleDigit(diffDuration.hours())+":"+ this.isSingleDigit(diffDuration.minutes())+" hours";
    let sendDuration = moment.duration(outDate.diff(inDate));
    let hours = sendDuration.asHours();
    this.visitor.expectedDurationInHours = Math.round(hours);
  }

  isSingleDigit(digit){
    console.log('isSingleDigit:', digit.toString().length, digit);
    if(digit <= 0){
      return '00';
    }else{
      return (digit.toString().length > 1) ? digit : '0'+digit;
    }
    
  }


}
